body {
  font-family: 'Josefin Sans', sans-serif;
  background-color: #D9D9D9;
  margin: auto;
}

header,
form label,
h3 {
  font-family: 'Acme', sans-serif;
}

h1 {
  font-size: 5vw;
  color: #4484CE;
}

h2 {
  font-size: 2vw;
}

h3 {
  margin-bottom: 0;
}



header p,
main label {
  font-size: 1.4vw;
}

button {
  padding: 5px 15px;
  background-color: #F19F4D;
  border: 1px solid #4484CE;
}

img {
  margin: auto;
  display: block;
  width: 100%;
}

ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  line-height: 1.5em;
  text-indent: 5px;
}


section * {
  padding: 0 2px;
}

span {
  font-weight: bold;
}

section>p {
  text-align: center;
  font-style: italic;
  margin-top: 0.3em;
}

/* Style the yelp images */
div section:nth-child(2) img {
  height: 75px;
  padding-bottom: 3px;
}

/* Style the movie images */
div section:nth-child(4) img {
  height: 150px;
}

.column-container {
  display: flex;
}

.column-container section {
  display: inline-block;
  width: 100%;
  border-style: groove;
  margin: 5px;
  background-color: #FFF;
}

.query-placeholder,
form,
header,
h3 {
  text-align: center;
}

form {
  margin: 10px auto;
}

.hide {
  display: none;
}

#input-search {
  padding: 5px;
  border: 1px solid #4484CE;
}

#map {
  margin-top: 10px;
  border: 2px solid #4484CE;
}

.results {
  float: left;
  width: 22%;
  margin: 10px;
  padding: 5px;
  border: 1px solid black;
  background-color: oldlace;
}

.results>img {
  width: 150px;
  height: 150px
}

#mapImg {
  width: 50%;
}

.App {
  margin: auto;
}

div.App>h2 {
  text-align: center;
}